import React from 'react';
import {
  Button, Modal, Form, Input, Cascader, Row,Col,Select
} from 'antd';


const { Option } = Select;
const residences = [{
  value: 'zhejiang',
  label: 'Zhejiang',
  children: [{
    value: 'hangzhou',
    label: 'Hangzhou',
    children: [{
      value: 'xihu',
      label: 'West Lake',
    }],
  }],
}, {
  value: 'jiangsu',
  label: 'Jiangsu',
  children: [{
    value: 'nanjing',
    label: 'Nanjing',
    children: [{
      value: 'zhonghuamen',
      label: 'Zhong Hua Men',
    }],
  }],
}];
const CollectionCreateForm = Form.create({ name: 'form_in_modal' })(
  // eslint-disable-next-line
  class extends React.Component {
    
    render() {
      const {
        visible, onCancel, onCreate, form,
      } = this.props;
      const { getFieldDecorator } = form;
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      return (
        <Modal
          visible={visible}
          title=""
          okText=""
          cancelText=""
          onCancel={onCancel}
          onOk={onCreate}
          className="pop"
          width={520}
        >
          <Form >
            <Form.Item l
            {...formItemLayout}
            label=""
            >
              {getFieldDecorator('name', {
                rules: [{ required: true, message: ' !' }],
              })(
                <Input />
              )}
            </Form.Item>
            <Form.Item 
            {...formItemLayout}
            label=""
            >
              {getFieldDecorator('card-number',{
                rules: [{ required: true, message: ' !' }],
              })(<Input />)}
            </Form.Item>
            <Form.Item 
            {...formItemLayout}
            label=""
            >
              {getFieldDecorator('bank-type')(
                <Select placeholder="">
                  <Option value="china"></Option>
                  <Option value="jh"></Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item 
            {...formItemLayout}
            label=""
            >
              {getFieldDecorator('residence', {
                initialValue: ['zhejiang', 'hangzhou', 'xihu'],
                rules: [{ type: 'array', required: true, message: 'Please select your habitual residence!' }],
              })(
                <Cascader options={residences} className="residences"/>
              )}
            </Form.Item>
            <Form.Item 
            {...formItemLayout}
            label=""
            >
              {getFieldDecorator('card-number',{
                rules: [{ required: true, message: ' !' }],
              })(<Input placeholder=""/>)}
            </Form.Item>
            <Form.Item
                {...formItemLayout}
                label=""
                //extra="We must make sure that your are a human."
            >
                <Row gutter={8}>
                <Col span={12}>
                    {getFieldDecorator('captcha', {
                    rules: [{ required: true, message: '  ' }],
                    })(
                    <Input />
                    )}
                </Col>
                <Col span={12}>
                    <Button  type="primary" style={{color:'#000'}}></Button>
                    <a href="#" style={{marginLeft:'5px'}}> </a>
                </Col>
                </Row>
            </Form.Item>
          </Form>
        </Modal>
      );
    }
  }
);

export class PopBindNew extends React.Component {

  state = {
    visible: false,
  };

  showModal = () => {
    this.setState({ visible: true });
  }

  handleCancel = () => {
    this.setState({ visible: false });
  }

  handleCreate = () => {
    const form = this.formRef.props.form;
    form.validateFields((err, values) => {
      if (err) {
        return;
      }

      console.log('Received values of form: ', values);
      form.resetFields();
      this.setState({ visible: false });
    });
  }

  saveFormRef = (formRef) => {
    this.formRef = formRef;
  }

  render() {
    return (
      <div>
        <div className="add-bank"  onClick={this.showModal}>+</div>
        <CollectionCreateForm
          wrappedComponentRef={this.saveFormRef}
          visible={this.state.visible}
          onCancel={this.handleCancel}
          onCreate={this.handleCreate}
        />
      </div>
    );
  }
}