import React from 'react';
import {
    Form, Row,Col,  Button, InputNumber,
} from 'antd';
 
  class PreAllTypeForm extends React.Component {
  
    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
        }
      });
    }

    render() {
      const { getFieldDecorator } = this.props.form;  
      const formItemLayout = {
        labelCol: {
          xs: { span: 16 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };

      return (
        <Form onSubmit={this.handleSubmit}>
          <Form.Item
            {...formItemLayout}
            label=""
          >
            {getFieldDecorator('priceInterest', {
              rules: [{ required: true, message: '!' }],
            })(
              <InputNumber 
                defaultValue={20}
              />
            )}
           </Form.Item>
           <Form.Item
            {...formItemLayout}
            label=""
          >
            {getFieldDecorator('priceMin', {
              rules: [{ required: true, message: '!' }],
            })(
              <InputNumber 
                defaultValue={10}
                formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                parser={value => value.replace(/\$\s?|(,*)/g, '')}
              />
            )}
          </Form.Item>
         <Row type="flex" justify="start" align="middle" style={{fontSize:'10px',margin:'20px 0'}}>
                <Col span={4} offset={4} className="red-bg"><br /></Col>
                <Col span={15} offset={1} >15
     <br />

2<br />

3<br />

4
     <br />
<br />


                </Col>
            </Row>
          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit"></Button>
          </Form.Item>
        </Form>
      );
    }
  }
  
 export const PreAllType4 = Form.create({ name: 'register' })(PreAllTypeForm);
