import React from 'react';
import {
    Form, Input, Button, Modal
  } from 'antd';
import { WrappedChangePhoneFormForm } from './sub/ChangePhoneForm.js';
  class PersonalForm extends React.Component {
    state = {
      confirmDirty: false,
      visible: false ,
    };
    showModal = () => {
        this.setState({
            visible: true,
        });
    }
    
    handleOk = (e) => {
        console.log(e);
        this.setState({
          visible: false,
        });
      }
    
    handleCancel = (e) => {
        console.log(e);
        this.setState({
          visible: false,
        });
      }
    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
        }
      });
    }
  
    handleConfirmBlur = (e) => {
      const value = e.target.value;
      this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    }

    render() {
      const { getFieldDecorator } = this.props.form;
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };
     
      const nextfixSelector = getFieldDecorator('nextfix', {
        
      })(
        <Button onClick={this.showModal} type="primary" style={{ width: 100 , margin:'-1px -12px'}}>
          
        </Button>
      );
      return (
        <Form onSubmit={this.handleSubmit}  style={{margin:'25px 0 0'}}>
            <Modal
                title=""
                visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                className="pop"
                footer={null}
                >
                <WrappedChangePhoneFormForm />
            </Modal>
            <Form.Item
                {...formItemLayout}
                label=""
            >
                {getFieldDecorator('nickname', {
                rules: [{ required: false, message: '!' }],
                })(
                <Input />
                )}
            </Form.Item>
            <Form.Item
                {...formItemLayout}
                label=""
            >
                {getFieldDecorator('realname', {
                rules: [{ required: false, message: '!' }],
                })(
                <Input />
                )}
            </Form.Item>
            <Form.Item
                {...formItemLayout}
                label=""
            >
                {getFieldDecorator('birthday', {
                rules: [{ required: false, message: '!' }],
                })(
                <Input />
                )}
            </Form.Item>
            <Form.Item
                {...formItemLayout}
                label=""
            >
                {getFieldDecorator('phone', {
                rules: [{ required: false, message: '' }],
                })(
                    <Input addonAfter={nextfixSelector} style={{ width: '100%' }} />
                )}
            </Form.Item>
            <Form.Item
                {...formItemLayout}
                label="QQ"
            >
                {getFieldDecorator('QQ', {
                rules: [{ required: false, message: 'QQ!' }],
                })(
                <Input style={{ width: '100%' }} />
                )}
            </Form.Item>
            <Form.Item
                {...formItemLayout}
                label="E-mail"
            >
                {getFieldDecorator('email', {
                rules: [{
                    type: 'email', message: '!',
                }, {
                    required: false, message: '',
                }],
                })(
                <Input />
                )}
            </Form.Item>
            <Form.Item {...tailFormItemLayout}>
                <Button type="primary" htmlType="submit"></Button>
            </Form.Item>
        </Form>
        );
    }
  }
  
  export const WrappedPersonalForm = Form.create({ name: 'personalForm' })(PersonalForm);
  
 