import React from 'react';
import {
    Form, Row,Col,  Button, Tag, Select,InputNumber,
} from 'antd';
  
const { Option } = Select;

class PreAllTypeForm extends React.Component {

    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
        }
      });
    }

    render() {
      const { getFieldDecorator } = this.props.form;  
      const formItemLayout = {
        labelCol: {
          xs: { span: 16 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };

      return (
        <Form onSubmit={this.handleSubmit}>        
          <Form.Item
            {...formItemLayout}
            label=""
          >
            {getFieldDecorator('gameName', {
              rules: [{ required: true, message: ''}],
            })(
                <Select >
                    <Option value="1"></Option>
                    <Option value="2"></Option>
                </Select>
            )}
          </Form.Item>
         <Row type="flex" justify="start" align="middle" style={{fontSize:'10px',margin:'20px 0'}}>
                <Col span={4} offset={4} className="red-bg"><br /></Col>
                <Col span={15} offset={1} >11<br />
25<br />
3,
                </Col>
            </Row>
          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit"></Button>
          </Form.Item>
        </Form>
      );
    }
  }
  
 export const PreAllType2 = Form.create({ name: 'register' })(PreAllTypeForm);
