import React, { Component } from 'react';
import {  Table ,Row,Col ,Button} from 'antd';

const columns = [{
    title: '',
    dataIndex: 'name',
    key: 'name',
  }];

const data = [{
    key: '1',
    name: '',
  }, {
    key: '2',
    name: '',
  }, {
    key: '3',
    name: '',
  }];

class SIM extends Component {
  
    render (){
        const rowSelection = {
            onChange: (selectedRowKeys, selectedRows) => {
              console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
            },
            getCheckboxProps: record => ({
              disabled: record.name === 'Disabled User', // Column configuration not to be checked
              name: record.name,
            }),
          };
        return (
            <div>
                <Row style={{borderLeft:'3px solid #ffc742',background:'#1f2429'}} >
                    <Col span={8} style={{fontSize:'18px',padding:'15px 20px'}}></Col>
                </Row>
                <Table 
                    rowSelection={rowSelection}
                    columns={columns} 
                    dataSource={data} 
                    pagination={false}
                    locale={{ emptyText: '' }}
                />
                <Row type="flex" justify="center">
                    <Col span={8} ><Button type="primary" style={{padding:'0 50px',margin:'30px 0'}}></Button></Col>
                </Row>
                <Row type="flex" justify="center" align="middle">
                    <Col span={2} style={{fontSize:10}}><span className="red-font"><br /></span></Col>
                    <Col span={20} >
                        <p className="note-wrap">* 1.<br />
                            * 2.<br />
                            * 3.</p>
                    </Col>
                </Row>
            </div>
        )
    }
}
export default SIM