import React from 'react';
import {
    Form, Row,Col,  Button, Tag, Select,InputNumber,
} from 'antd';
  
const { CheckableTag } = Tag;
const { Option } = Select;

class MyTag extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            checked: true
        };
      }

  handleChange = (checked) => {
    this.setState({ checked });
  }

  render() {
    return <CheckableTag {...this.props} checked={this.state.checked} onChange={this.handleChange} />;
  }
}
function MyTagGroup (props){
    return (
        <div>
            { 
                props.todos.map( (todo,i) => 
                    <MyTag index={i} key={todo.id} text={todo.text}>{todo.text}</MyTag>
                )
            }
        </div>
    )
}
  
  class PreAllTypeForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = { todos: [{ id: 1, text: ''}, { id: 2, text: ''}, { id: 3, text: ''}, { id: 4, text: ''}] };
    }
  
    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
        }
      });
    }

    render() {
      const { getFieldDecorator } = this.props.form;  
      const formItemLayout = {
        labelCol: {
          xs: { span: 16 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };

      return (
        <Form onSubmit={this.handleSubmit}>
          <Form.Item
            {...formItemLayout}
            label=""
          >
            {getFieldDecorator('gameType', {
              rules: [{
                validator: this.checkType
              }],
            })(
              <MyTagGroup todos={this.state.todos} />
            )}
          </Form.Item>
        
          <Form.Item
            {...formItemLayout}
            label=""
          >
            {getFieldDecorator('gameName', {
              rules: [{ required: true, message: ''}],
            })(
                <Select >
                    <Option value="1">30%</Option>
                    <Option value="2">100%</Option>
                    <Option value="3">15%-30%</Option>
                </Select>
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label=""
          >
            {getFieldDecorator('price', {
              rules: [{ required: true, message: '!' }],
            })(
              <InputNumber 
                defaultValue={2}
                formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                parser={value => value.replace(/\$\s?|(,*)/g, '')}
              />
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label=""
          >
            {getFieldDecorator('priceMin', {
              rules: [{ required: true, message: '!' }],
            })(
              <InputNumber 
                defaultValue={10}
                formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                parser={value => value.replace(/\$\s?|(,*)/g, '')}
              />
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label=""
          >
            {getFieldDecorator('priceInterest', {
              rules: [{ required: true, message: '!' }],
            })(
              <InputNumber 
                defaultValue={0}
                formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                parser={value => value.replace(/\$\s?|(,*)/g, '')}
              />
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label=""
          >
            {getFieldDecorator('priceInterest', {
              rules: [{ required: true, message: '!' }],
            })(
              <InputNumber 
                defaultValue={20}
              />
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label=""
          >
            {getFieldDecorator('priceInterest', {
              rules: [{ required: true, message: '!' }],
            })(
              <InputNumber 
                defaultValue={20}
              />
            )}
           </Form.Item>
         <Row type="flex" justify="start" align="middle" style={{fontSize:'10px',margin:'20px 0'}}>
                <Col span={4} offset={4} className="red-bg"><br /></Col>
                <Col span={15} offset={1} >1<br />
2,,<br />
3<span className="bule-font"></span><br />
                </Col>
            </Row>
          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit"></Button>
          </Form.Item>
        </Form>
      );
    }
  }
  
 export const PreAllType1 = Form.create({ name: 'register' })(PreAllTypeForm);
