import React, { Component } from 'react';
import {   Row ,Col,Form,Radio,Input ,Button,Tag} from 'antd';

class weixinForm extends React.Component {
    state = {
        confirmDirty: false,
        value: '',
        getMoneyMethod: '',
    };

    handleSubmit = (e) => {
      e.preventDefault();

      this.props.form.setFieldsValue({
        zhifuMethod : '1',
      });
      
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
          //this.props.handleLink(values.getMoneyMethod)
            this.setState({
                getMoneyMethod:values.getMoneyMethod,
            });
        }
      });
    }
    onChange = (e) => {
        console.log('radio checked', e.target.value);
        this.setState({
            value: e.target.value,
        });
    }
    render (){
        const { getFieldDecorator } = this.props.form;
        const style = {
            width:100,
            display:'inline-block',
            position:'absolute',
            top: '65px',
            left: '44px',
        } 

        const formItemLayout = {
            colon: false,
            labelCol: { span: 1 },
            wrapperCol: { span: 17 },
          }
        const tailFormItemLayout = {
            wrapperCol: {
              xs: {
                span: 23,
                offset: 4,
              },
              sm: {
                span: 17,
                offset: 4,
              },
            },
          };
          
        let page ;
             if(this.state.getMoneyMethod){
            page = (
                <div>
                    <p className="red-font"></p>
                    <Row type="flex" justify="start" align="middle" style={{padding:"10px 0",background:"#141414"}}>
                        <Col span={20} offset={2}></Col>
                    </Row>
                    <Row type="flex" justify="start" align="middle" style={{padding:"10px 0",background:"#1f2125"}}>
                        <Col span={4} offset={3} className="gray-font"></Col>
                        <Col span={12}></Col>
                    </Row>
                    <Row type="flex" justify="start" align="middle" style={{padding:"10px 0"}}>
                        <Col span={4} offset={3} className="gray-font"></Col>
                        <Col span={12}>kaixin2018</Col>
                    </Row>
                    <Row type="flex" justify="start" align="middle" style={{padding:"10px 0",background:"#1f2125"}}>
                        <Col span={4} offset={3} className="gray-font"></Col>
                        <Col span={12}><span className="red-font">1000</span>&nbsp;<span style={{fontSize:'10px'}}></span></Col>
                    </Row>
                    <Row type="flex" justify="start" align="middle" style={{padding:"10px 0"}}>
                        <Col span={4} offset={3} className="gray-font"></Col>
                        <Col span={12}>998</Col>
                    </Row>
                    <Row type="flex" justify="space-around" align="middle" style={{fontSize:'10px',margin:'20px 0'}}>
                        <Col span={2} offset={1} className="red-bg"><br /></Col>
                        <Col span={21}><span className="red-font">10</span><a href="#"></a>
<br /></Col>
                    </Row>
                    <Row type="flex" justify="start" align="middle">
                        <Col span={8}><Button type="primary"></Button></Col>
                        <Col span={4}><a></a></Col>
                    </Row>
                    
                </div>            
            )
        }else{
            page = (
            <Form onSubmit={this.handleSubmit}>
                <Form.Item
                    {...formItemLayout}
                    label=""
                >
                    {getFieldDecorator('zhifuMethod')(
                        <Tag color="gold" style={{textAlign:'center',height:60,padding:'10px'}}><span style={{display:'block',fontSize:'0.03em'}}></span></Tag>
                    )}
                    <p className="note-wrap" >1%<br/>
    <br/>
    </p>
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label=""
                >
                    {getFieldDecorator('tongdaoMethod')(
                        <Radio.Group initialValue="1" buttonStyle="solid">
                            <Radio.Button value="1" style={{marginRight:8}}>1</Radio.Button>
                            <Radio.Button value="2" style={{marginRight:8}}>2</Radio.Button>
                        </Radio.Group>
                    )}
                    <br /><p className="gray-font"></p>
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label=""
                >
                    {getFieldDecorator('getMoneyMethod')(
                        <Radio.Group onChange={this.onChange} value={this.state.value}  buttonStyle="solid">
                            <Radio.Button value={50} style={{marginRight:8}}>50</Radio.Button>
                            <Radio.Button value={100} style={{marginRight:8}} checked={true}>100</Radio.Button>
                            <Radio.Button value={200} style={{marginRight:8}}>200</Radio.Button>
                            <Radio.Button value={500} style={{marginRight:8}}>500</Radio.Button>
                            <Radio.Button value={1000} style={{marginRight:8}}>1000</Radio.Button>
                            <Radio.Button value={2000} style={{marginRight:8}}>2000</Radio.Button>
                            <Radio.Button value={3000} style={{marginRight:8}}>3000</Radio.Button><br />
                            <Radio  value={50000}>
                                {this.state.value === 50000 ? <Input placeholder="11-5" style={{ width: 100, marginLeft: 10 }} /> : null}
                            </Radio>
                            <br /> <span className="red-font">100</span> 
                        </Radio.Group>
                    )}
                </Form.Item>
                <Form.Item {...tailFormItemLayout}>
                    <Button type="primary" htmlType="submit" style={{padding:'0 50px',marginTop:30}}></Button>
                </Form.Item>
            </Form>
            )
        }
        return (
            
            <div className="recharge">
                {page}
            </div>
        )
    }
}

 const weixin = Form.create({ name: 'weixin' })(weixinForm);

 export default weixin
