class Greeting extends Component {
    state = {
        visible: false,
        confirmLoading: false,
        userName:'',
        isLogin: false,
      }
    handleLogin =(userName) => {
        this.setState({
            userName,
            visible:false,
        })
    }
    showModal = () => {
        this.setState({
          visible: true,
        });
    }

    handleOk = () => {
      
        setTimeout(() => {
          this.setState({
            visible: false,
            confirmLoading: false,
          });
        }, 2000);
    }
    
    handleCancel = () => {
        console.log('Clicked cancel button');
        this.setState({
          visible: false,
        });
    }

    render(){
        let userMessage;
        const style = {
            fontSize:21,
            margin:"0 3px 0 15px",
        }
        const { visible, confirmLoading } = this.state;
        if(this.state.userName){
            userMessage = (
                <div>
                    <Badge count={5}><Icon type="mail" style={style}/></Badge>
                    <Dropdown overlay={menuFastAccount}>
                        <a className="ant-dropdown-link" href="#">
                        <Link to="/Account"><Icon type="user" style={style}/>{this.state.userName}</Link>
                        </a>
                    </Dropdown>
                </div>);
        }else{
            userMessage = (
                <div>
                    <Button onClick={this.showModal} type="primary" value="small" ghost style={{ padding:'0 30px',marginRight:10, }} ></Button>
                    <Link to="/register"><Button type="primary" value="small"></Button></Link>
                </div>
            )
        }
        return (
            <div>
                <Modal
                    title=""
                    visible={visible}
                    onOk={this.handleOk}
                    confirmLoading={confirmLoading}
                    onCancel={this.handleCancel}
                    width={300}
                    footer={null}
                    className="pop"
                    destroyOnClose
                    >
                    <WrappedNormalLoginForm handleLogin={this.handleLogin} />
                </Modal>
                {userMessage}
            </div>
        )
    };
}

